.App {
  text-align: center;
}

.App {
  --text-color: hsl(200, 15%, 8%);
  --bg-color: hsl(0, 0%, 98%);
  --elements: hsl(0, 0%, 100%);
}

.App.dark-theme {
  --text-color: hsl(0, 0%, 100%);
  --bg-color: hsl(207, 26%, 17%);
  --elements: hsl(209, 23%, 22%);
}

