.App {
  background-color: var(--bg-color);
  color: var(--text-color);
}

.header-container {
  background-color: var(--elements);
}

.moon {
  color: var(--text-color);
}

.search {
  background-color: var(--elements);
}

.filter {
  background-color: var(--elements);
}

.country-wrapper {
  background-color: var(--elements);
}

.btn {
  color: var(--text-color);
  background-color: var(--elements);
}
